<template>
    
    <div class="card" id="section-to-print">
        <div class="card-header">
            <div class="alert alert-primary" v-bind:class="{'alert-success': prescriptionData.status == 'completed'}">{{ prescriptionData.status }}</div>
            <div class="d-flex justify-content-between mb-3">
                <div>
                    <span class="badge text-bg-warning">{{ prescriptionData.priority }}</span>
                </div>
                <button class="btn btn-outline-primary print-hide" onclick="window.print()">
                    <vue-feather class="align-middle icon-margin" type="printer"></vue-feather>
                    <span class="align-middle">Ausdrucken</span>
                </button>
            </div>
            <h2 class="text-primary">Prescription</h2>
            <div class="mb-3">{{ formatDate(prescriptionData.authoredOn) }}</div>
            <div class="text-muted">{{ prescriptionData.id }}</div>
        </div>
        
        <ul class="list-group list-group-flush">
            <li class="list-group-item">
                <div v-if="patientData.id != null && patientData.name[0] != null">
                    <h5>
                        {{ patientData.name[0].given + " " + patientData.name[0].family }}
                    </h5>
                    <div class="form-group row">
                        <div class="col-md-3"><b>Geburtstag</b></div>
                        <div class="col-md-9">{{ patientData.birthDate }}</div>
                    </div>
                    <div class="form-group row">
                        <div class="col-md-3"><b>Geschlecht</b></div>
                        <div class="col-md-9">{{ patientData.gender }}</div>
                    </div>
                    <div class="form-group row">
                        <div class="col-md-3"><b>Tel. Nr.</b></div>
                        <div class="col-md-9">{{ patientData.telecom[0].value }}</div>
                    </div>
                    <div class="form-group row">
                        <div class="col-md-3"><b>Adresse</b></div>
                        <div class="col-md-9">
                            {{ patientData.address[0].line[0] + ", " + patientData.address[0].city }}
                        </div>
                    </div>
                </div>
            </li>
            <div v-if="prescriptionLoaded && prescriptionData.medicationCodeableConcept.coding[0]" class="preventBreak">
                <li class="list-group-item">
                    <h5>Medikament</h5>
                    <div>{{ prescriptionData.medicationCodeableConcept.coding[0].display }}</div>
                </li>
            </div>
            <div v-if="prescriptionLoaded && prescriptionData.medicationCodeableConcept.coding[0]" class="preventBreak">
                <li class="list-group-item">
                    <h5>Code</h5>
                    <div>
                        <a target="_blank"
                            :href="prescriptionData.medicationCodeableConcept.coding[0].system + '/' + prescriptionData.medicationCodeableConcept.coding[0].code">
                            {{ prescriptionData.medicationCodeableConcept.coding[0].code }}
                        </a> - <span class="text-muted">{{ prescriptionData.medicationCodeableConcept.coding[0].system }}</span>
                    </div>
                </li>
            </div>
            <div class="preventBreak">
                <li class="list-group-item">
                    <h5>Diagnose/Begründung</h5>
                    <div>Lorem ipsum dolor sit amet</div>
                </li>
            </div>
            <div v-if="prescriptionLoaded && prescriptionData.dosageInstruction" class="preventBreak">
                <li class="list-group-item">
                    <h5>Einnahmeregel</h5>
                    <div v-for="(instruction, index) in prescriptionData.dosageInstruction" :key="index">
                        <p>{{ instruction.text }}</p>
                    </div>
                </li>
            </div>
            <div class="preventBreak">
                <li class="list-group-item">
                    <h5>Anmerkung</h5>
                    <div v-for="(note, index) in prescriptionData.note" :key="index">
                        <p>{{ note.text }}</p>
                    </div>
                </li>
            </div>
        </ul>
    </div>
    <div v-if="prescriptionData.status == 'active'" id="bottom-buttons-container" class="d-flex justify-content-center mb-4 mx-3">
        <button id="completed-button" class="btn btn-success"
            @click="updatePrescriptionObjStatus('completed')">
            <vue-feather class="align-middle icon-margin" type="check-circle"></vue-feather>
            <span class="align-middle inline-block">Erledigt</span>
        </button>
        <button id="cancel-button" class="btn btn-outline-danger ml-auto"
            @click="updatePrescriptionObjStatus('cancelled')">
            <vue-feather class="align-middle icon-margin" type="x-octagon"></vue-feather>
            <span class="align-middle">Stornieren</span>
        </button>
    </div>
</template>
<script>
export default ({
    name: "Care Plans",
    data() {
        return {
            prescriptionUrl: '/MedicationRequest/' + this.$route.params.prescriptionId,
            patientUrl: '/Patient/' + this.$route.params.patientId,
            prescriptionData: {},
            patientData: {},
            patientLoaded: false,
            prescriptionLoaded: false
        }
    },
    beforeMount() {
        console.log(this.$apiUrl + this.patientUrl)
        console.log(this.$apiUrl + this.prescriptionUrl)
        fetch(this.$apiUrl + this.prescriptionUrl, {
            credentials: 'include',
        })
            .then(response => response.json())
            .then(data => {
                console.log(data)
                this.prescriptionData = data
                this.prescriptionLoaded = true
            })

        fetch(this.$apiUrl + this.patientUrl, {
            credentials: 'include',
        })
            .then((response) => response.json())
            .then(data => {
                console.log(data)
                this.patientData = data
                this.patientLoaded = true
            })
    },
    methods: {
        formatDate(element) {
            const date = new Date(element).toLocaleDateString('de-AT', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
            });
            return date
        },
        updatePrescriptionObjStatus(newStatus) {
            this.prescriptionData.status = newStatus;
            this.updatePrescription()
        },
        updatePrescription() {
            const postRequest = {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(this.prescriptionData),
                credentials: 'include',
            };
            fetch(
                `${this.$apiUrl}/MedicationRequest`,
                postRequest
            )
                .then(async (response) => {
                    const data = await response.json();
                    if (!response.ok) {
                        //todo -> display error message
                        const error = (data && data.message) || response.status;
                        console.log(Promise.reject(error));
                    } else if (data != null) {
                        console.log(data);
                    }
                })
                .catch((error) => {
                    //todo -> display error message
                    console.error("Error", error);
                });
        }
    }
})
</script>

<style scoped>
.card {
    text-align: left;
    margin: 2em 1em;
}

.card-header {
    padding: 3rem;
}

li {
    padding: 2rem;
}

.icon-margin{
    margin-right: 0.3rem;
}

#cancel-button {
    right: 1em;
    margin-left: auto;
}

#completed-button {
    margin-left: auto;
    transform: translate(50%);
}

</style>

<style scoped>
@media print {
    body * {
        visibility: hidden;
    }

    #section-to-print,
    #section-to-print * {
        visibility: visible;
    }

    #section-to-print {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        margin: 0;
    }

    .card-header {
        padding-top: 1.5rem !important;
        padding-bottom: 1.5rem !important;

    }

    li {
        padding-top: 1rem !important;
        padding-bottom: 1rem !important;
    }

    .print-hide{
        display: none;
    }
}
</style>